
.contacts-section{
   margin: 1em;
   margin-top: 12em;

   .contact-h3{
    color: #BC5252;
    font-size: 3rem;
    margin-left: 0 !important;
   }

    p{
        color: #444;
        margin-left: 0 !important;
        font-size: 0.9rem;
        width: 90%;
        margin-top: 8em;
        margin-bottom: 13em;
   }
}


@media screen and (min-width: 680px) {
    .contacts-section{
        margin-top: 15em !important;
    }
    
}


@media only screen and (min-width: 1190px) {
    .contacts-section{
        margin-left: auto !important;
        margin-right: auto !important;
        max-width: 1190px !important;
    }
}